/**
 * @module {Object} event  
 * @return {object} 
 * @property {function} stopBubble 
 * @property {function} stopBubbleEx 
 * @author ljquan@qq.com
 */

module.exports = {
    /**
     * 
     * @param  {event} e 
     * @return {null}
     */
    stopBubble: function(e) {
        if (e && e.stopPropagation) {
            e.stopPropagation();
        }
        e.preventDefault();
    },
    /**
     * 
     * @param  {event} e 
     * @return {null}
     */
    stopBubbleEx: function(e) {
        if (e && e.stopPropagation) {
            e.stopPropagation();
        }
    }
};



